export default {
	name: 'EvanSwitch',
	props: {
		value: {
			type: [String, Number, Boolean],
			default: false
		},
		activeColor: {
			type: String,
			default: '#108ee9'
		},
		inactiveColor: {
			type: String,
			default: '#fff'
		},
		size: {
			type: Number,
			default: 40
		},
		disabled: {
			type: Boolean,
			default: false
		},
		activeValue: {
			type: [String, Number, Boolean],
			default: true
		},
		inactiveValue: {
			type: [String, Number, Boolean],
			default: false
		},
		beforeChange: {
			type: Function,
			default: null
		},
		extraData: null
	},
	computed: {
		switchHeight() {




			return this.size + 'rpx'

		}
	},
	watch: {
		value: {
			immediate: true,
			handler(value) {
				this.currentValue = value
			}
		}
	},
	data() {
		return {
			currentValue: false
		}
	},
	methods: {
		toggle() {
			if (!this.disabled) {
				if (this.beforeChange && typeof this.beforeChange === 'function') {
					const result = this.beforeChange(this.currentValue === this.activeValue ? this.inactiveValue : this.activeValue,
						this.extraData)
					if (typeof result === 'object') {
						result.then(() => {
							this.toggleValue()
						}).catch(() => {})
					} else if (typeof result === 'boolean' && result) {
						this.toggleValue()
					}
				} else {
					this.toggleValue()
				}
			}
		},
		toggleValue() {
			this.currentValue = this.currentValue === this.activeValue ? this.inactiveValue : this.activeValue
			this.$emit('input', this.currentValue)
			this.$emit('change', this.currentValue)
		}
	}
}
